import Vue from 'vue';
import './theme';
import './styles.scss';
import router from './router';
import AppComponent from './app/app';
console.log('Environment:', process.env.NODE_ENV);
var v = new Vue({
    router: router,
    el: '#app',
    components: { AppComponent: AppComponent },
    render: function (h) { return h(AppComponent); }
});



